h1{
    text-align: center;
  }
  .gallery {
    padding: 1.5rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }
  
  .gallery-item {
    width: 45%;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
  
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .gallery-item img {
    width: 100%;
    height: auto;
  }
  
  .description {
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
  
  .learn-more {
    display: inline-block;
    margin: 10px 0;
    padding: 8px 16px;
    color: #fff;
    background-color: #007bff;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .learn-more:hover {
    background-color: #0056b3;
  }

@media only screen and (max-width:900px) {
  
    .gallery-item{
      width: 100%;
    }
    
  }