.body{
background: url("./assets/back.png");
background-repeat: no-repeat;
background-size: cover;
box-sizing: border-box;
padding: 0rem 4.5rem;
}


.header{
  padding: 0rem 1.1rem;
}
.App {
  text-align: center;

}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header{
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid rgba(238, 233, 233, 0.322);
}
.nav{
  display: flex;
  justify-content: center;
  padding: 1rem 1rem;
}
.nav-item{
  text-decoration: none;
  color: white;
  padding: 10px 15px;
  margin: 10px 10px;
  border-radius: 10px;
  align-self: center;
  
}
.nav-item:hover{
  background-color:#6265fb;
}
nav a.active{
  background-color: #6265fb;
}
/* burger menu settings */
.hr{
  margin: 0;
  background-color: #ffffff;
  border-color: transparent;
  width: 100%;
}
.mobile-header {
  height: 3.3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(238, 233, 233, 0.322);
}

.mobile-header button {
  border: 0;
  background-color: #6265fb;
  border: 1px solid #ffffff;
  padding: 0px 15px;
  border-radius: 10px;
}

.nav-menu2 {
  padding: 1rem 0;
  margin: 0;
  visibility: hidden;
  transform-origin: left center;
  transform: translateX(-100%);
  transition: visibility 0.5s, transform 0.5s;
  position: absolute;
  top: 102px;
  left: 0;
  width: 100%;
  /* Adjust the width as needed */
  height: 100%;
  /* Full height of the viewport */
  background: url("./assets/back.png");
background-repeat: no-repeat;
background-size: cover;
}

.nav-menu2.active {
  background: url("./assets/back.png");
background-repeat: no-repeat;
background-size: cover;
  position: absolute;
  top: 102px;
  z-index: 5;
  width: 100%;
  visibility: visible;
  transform: translateX(0);
}

.mobile-header ul {
  list-style-type: none;
}

.nav-menu2 li {
  padding: 20px 25px;
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width:900px) {
  .header {
    display: none;
}
.mobile-header{
  height: 100px;
}
a.active{
  background-color: #6265fb;
}
.nav-item{
  
  color: black;}

  .hr{
    background-color: black;
  }
}
@media only screen and (min-width:900px) {
  .mobile-header {
    display: none;
}
.nav-item{
  
  color: black;}

  .hr{
    background-color: black;
  }
}