.intro-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 0;
  }
  .main-home{
    height: 100vh;
  }
  .intro-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .intro-text h1 {
    font-size: 2.5em;
    margin: 0;
  }
  
  .intro-text .highlight {
    color:#6265fb; /* Highlight color */
  }
  
  .intro-text p {
    font-size: 1.2em;
    margin: 10px 0 20px;
    text-align: center;
  }
  
  .about-btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-size: 1.2rem;
  }
  
  .about-btn:hover {
    background-color: #0056b3;
  }
  
  .social-icons {
    margin-top: 20px;
  }
  
  .social-icons a {
    margin-right: 10px;
    display: inline-block;
  }
  
  .social-icons img {
    width: 70px;
    height: 70px;
    transition: transform 0.3s ease;
  }
  
  .social-icons img:hover {
    transform: scale(1.1);
  }
  
  .intro-image {
    max-width: 50%;
    display: flex;
    justify-content: end;
  }
  
  .intro-image img {
    width: 400px;
    border-radius: 50%;
  }
  @media only screen and (max-width:900px) {
    .body{
      padding: 0px 2rem;
    }
    .header{
      padding: 0;
    }
    .intro-text{
      width: 100%;
    }
    .intro-section{
      flex-direction: column;
      gap: 20px;
      padding: 2rem 0rem;
    }
    .intro-image img{
      width: 100%;
    }
    .social-icons img {
      width: 50px;
      height: 50px;
      transition: transform 0.3s ease;
    }
    
  }